<template>
    <div class="page" ref="page">
        <div class="container" ref="container">
            <div class="card-box search_container">
                <el-form inline label-width="80px" label-position="right" size="small" :model="filterParams">
                    <el-form-item label="">
                        <el-input placeholder="" v-model.trim="filterParams.title" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select
                            class="source-from"
                            placeholder=""
                            v-model="filterParams.task_status"
                            clearable
                        >
                            <el-option value label=""></el-option>
                            <el-option value="enable" label=""></el-option>
                            <el-option value="disable" label=""></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="filterParams.task_cycle" clearable></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="getTaskList('cx')"></el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="addTask"></el-button>
                    </el-form-item>
                </el-form>
            </div>
            <div class="card-box table_container">
                <div class="select-all btns"></div>
                <el-table
                    style="width: 100%"
                    align="center"
                    :data="tableData"
                    empty-text=""
                    v-loading="tableLoading"
                    element-loading-text=""
                    element-loading-spinner="el-icon-loading"
                    @selection-change="handleSelectionChange"
                    @row-click="handleClickRow"
                    ref="eltableCurrentRow"
                >
                    <el-table-column label="" prop="title"></el-table-column>
                    <el-table-column label="" prop="task_name"></el-table-column>
                    <el-table-column label="ID" prop="task_id"></el-table-column>
                    <el-table-column label="" prop="task_cycle" width="120px"></el-table-column>
                    <el-table-column label="" prop="task_type"></el-table-column>
                    <el-table-column label="" prop="task_status">
                        <template slot-scope="scoped">
                            <span>{{ scoped.row.task_status === 'enable' ? '' : '' }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="" width="190px">
                        <template slot-scope="scoped">
                            <span v-if="scoped.row.start_time <= 0"></span>
                            <span v-else>
                                {{ new Date(scoped.row.start_time).toLocaleDateString() }}
                                {{ new Date(scoped.row.end_time).toLocaleDateString() }}
                            </span>
                        </template>
                    </el-table-column>
                    <el-table-column label="" width="100px">
                        <div class="btns" slot-scope="scoped">
                            <el-button type="info" size="mini" @click.stop="editTask(scoped.row)"></el-button>
                            <el-button type="warning" size="mini" @click.stop="handelDel(scoped.row)"></el-button>
                        </div>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <el-pagination
            background
            class="pagination"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="15"
            layout="total, prev, pager, next"
            :total="totalCount"
        ></el-pagination>
    </div>
</template>

<script>
export default {
    components: {},
    data () {
        return {
            tableData: [
                {
                    title: '',
                    task_id: '',
                    task_type: '',
                    task_cycle: ''
                }
            ],
            filterParams: {
                page: 1,
                page_size: 15,
                title: '',
                task_status: '',
                task_cycle: ''
            },
            list: [],
            currentPage: 1,
            totalCount: 10,
            currentType: 1,
            tableLoading: false,
            flag: false
        };
    },
    computed: {},
    watch: {},
    //  - DOM
    mounted () {
        this.getTaskList();
    },
    methods: {
        editTask (row) {},
        addTask () {},
        handelDel (task) {},
        getTaskList (type) {
            if (type === 'cx') {
                this.filterParams.page = 1;
            }
            let params = {
                ...this.filterParams
            };
            this.tableLoading = true;
            this.$mapi.getTaskList(params).then(res => {
                this.tableLoading = false;
                let list = res.data.list;
                this.totalCount = res.data.total;
                list.forEach(item => {
                    if (item.start_time <= 0 && item.end_time <= 0) {
                        item.expiration = 1;
                    } else {
                        item.expiration = 2;
                        item.start_time *= 1000;
                        item.end_time *= 1000;
                        item.date = [item.start_time, item.end_time];
                    }
                });
                this.tableData = list;
            });
        },
        handleCurrentChange (page) {
            this.currentPage = page;
            this.filterParams.page = page;
            this.getTaskList();
        },
        handleSelectionChange (val) {},
        handleClickRow (row, col, event) {
            this.flag = !this.flag;
            this.$refs.eltableCurrentRow.toggleRowSelection(row, this.flag);
        }
    }
};
</script>
<style lang="less" scoped>
.page {
    width: 100%;
    .container {
        overflow-y: auto;
        width: 100%;
        padding: 10px 20px 0 20px;
        box-sizing: border-box;
        margin-bottom: 40px;
    }
    .pagination {
        position: fixed;
        width: 100%;
        left: 0;
        bottom: 20px;
        text-align: right;
        background: #fff;
        z-index: 10;
    }
}
.btns {
    button {
        width: 60px;
        padding: 3px 0;
        margin: 0 5px;
    }
}
</style>
